// npm i styled-reset설치 후 사용
import "../style/Login.css";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import mainlogo from "../images/mainlogo.png";


function Login() {
  const navigate = useNavigate();
  const [formDataLogin, setFormDataLogin] = useState({
    id: "",
    psword: "",
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormDataLogin({
      ...formDataLogin,
      [name]: value,
    });
  };

  async function handleSubmitLogin(e) {
    e.preventDefault();
    if (formDataLogin.id === "" || formDataLogin.psword === "") {
      // 아이디 또는 비밀번호가 비어있는 경우
      alert("아이디와 비밀번호를 모두 입력하세요.");
      return; // 함수 종료
    }
    // 정보 전달 함수
    try {
      const response = await fetch('/api/user/login', {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formDataLogin),
      });
      if (response.ok) {
        const res = await response.json();
        if (res.success) {
          if(res.data === 1)
            navigate("/PatientListD");
          else if(res.data === 2)
            navigate("/PatientListT");
        } else {
          alert(res.msg);
        }
      } else {
        throw Error("서버 응답 실패");
      }
    } catch (err) {
      console.error(Error('로그인 중 에러 발생'));
    }
  };

  return (
    <div className="login">
      <header className="login-text">
        <h1 className="login-text-h1">
          Login
        </h1>
      </header>

      <div className="login-input">
        <div className="login-input-group">
          <div className="login-input-id">
            <span className="login-input-id-text">ID</span>
            <input type="text" name = "id" className="login-input-id-username" 
            value={formDataLogin.id} onChange={handleInputChange}/>
          </div>
          <div className="login-input-pw">
            <span className="login-input-pw-text">Password</span>
            <input type="password" name = "psword" className="login-input-id-password"
            value={formDataLogin.psword} onChange={handleInputChange} />
          </div>
        </div>
          <input className="login-button" type="submit" value="Enter" onClick={handleSubmitLogin}/>
      </div>
      <img className="mainlogo" src={mainlogo} alt="mainlogo" />
    </div>
  );
}


export default Login;